:root {
  --header-bg: #fff;
  --header-border-color: #111;
  --header-menu-text-color: #111;
  --header-shippinglist-bg: #eac645;
  --header-user-bg: #4f2ee8;
  --header-user-text-color: #fff;
  --header-logo-text-color: #111;
  --header-modal-mobile-color: #111;
}

:root[theme="dark"] {
  --header-bg: #111;
  --header-border-color: #fff;
  --header-menu-text-color: #fff;
  --header-shippinglist-bg: #eac645;
  --header-user-bg: #4f2ee8;
  --header-user-text-color: #fff;
  --header-logo-text-color: #f3f3f3;
  --header-modal-mobile-color: #fff;
}

*, :before, :after {
  box-sizing: border-box;
}

h1, h2, h3, h4, h5, h6, p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 0;
}

img {
  max-width: 100%;
  display: block;
}

a {
  text-decoration: none;
}

body {
  font-family: DM Sans, sans-serif;
}

.link {
  text-decoration: none;
}

.paragraf {
  font-size: 16px;
}

.list {
  list-style: none;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  max-width: 375px;
  margin: 0 auto;
  padding: 0 20px;
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    max-width: 1440px;
    padding: 0 24px;
  }
}

header {
  max-width: 375px;
  border: 1.5px solid var(--header-border-color);
  background: var(--header-bg);
  border-radius: 0 0 8px 8px;
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 18px 20px;
  display: flex;
}

.header-nav {
  display: flex;
}

.header-logo {
  cursor: pointer;
  justify-content: center;
  align-items: center;
  gap: 8px;
  display: flex;
}

.header-logo-img {
  width: 24px;
  height: 24px;
}

.header-logo-text {
  width: 77.001px;
  height: 13.266px;
  fill: var(--header-logo-text-color);
}

.header-menu-list {
  display: none;
}

.header-buttons {
  justify-content: center;
  align-items: center;
  gap: 14px;
  display: flex;
}

.header-switcher {
  cursor: pointer;
  background-color: #0000;
  border: none;
}

.header-switcher-img {
  width: 40px;
  height: 24px;
}

.header-user {
  display: none;
}

.header-modal-mobile-link {
  cursor: pointer;
}

.header-modal-mobile {
  width: 28px;
  height: 28px;
  stroke: var(--header-modal-mobile-color);
}

@media screen and (min-width: 768px) {
  header {
    max-width: 768px;
    padding: 14px 32px;
  }

  .header-nav {
    gap: 40px;
  }

  .header-menu-list {
    align-items: center;
    gap: 24px;
    display: flex;
  }

  .header-menu-home {
    color: var(--header-menu-text-color);
    letter-spacing: -.56px;
    text-transform: uppercase;
    font-size: 14px;
    font-style: normal;
    font-weight: 500;
    line-height: 128.571%;
  }

  .header-menu-shippinglist {
    background: var(--header-shippinglist-bg);
    color: var(--header-menu-text-color);
    text-transform: uppercase;
    border-radius: 36px;
    justify-content: center;
    align-items: center;
    gap: 8px;
    padding: 7px 16px;
    font-size: 14px;
    font-style: normal;
    font-weight: 700;
    line-height: 128.571%;
    display: flex;
  }

  .header-menu-shippinglist-icon {
    width: 20px;
    height: 20px;
    fill: var(--header-menu-text-color);
  }

  .header-user {
    cursor: pointer;
    background: var(--header-user-bg);
    border: none;
    border-radius: 36px;
    justify-content: center;
    align-items: center;
    padding: 4px 12px;
    display: flex;
  }

  .header-user-logo {
    width: 37px;
    height: 37px;
    border-radius: 50%;
    margin-right: 9px;
  }

  .header-user-name {
    color: var(--header-user-text-color);
    letter-spacing: -.44px;
    margin-right: 4px;
    font-size: 22px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
  }

  .header-user-icon-down {
    width: 23px;
    height: 26px;
  }

  .header-modal-mobile {
    display: none;
  }
}

@media screen and (min-width: 1440px) {
  header {
    max-width: 1440px;
    padding: 14px 24px;
  }
}

.auth-reg-modal {
  background-color: var(--bg-modal-box);
  width: 100%;
  height: 100%;
  background: #4f2ee8;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
}

.auth-reg-modal-callback-form {
  opacity: 1;
  width: 295px;
  background: #f6f6f6;
  border: 2px solid #111;
  border-radius: 18px;
  flex-direction: column;
  padding: 40px 20px;
  display: flex;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.auth-reg-modal-close {
  width: 20px;
  height: 20px;
  cursor: pointer;
  background-color: #0000;
  border: none;
  justify-content: center;
  align-items: center;
  padding: 0;
  display: flex;
  position: absolute;
  top: 14px;
  right: 14px;
}

.auth-reg-modal-input-box {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 24px;
  margin-bottom: 24px;
  display: flex;
}

.auth-reg-modal-field-input {
  width: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: relative;
}

.auth-reg-modal-input {
  width: 100%;
  letter-spacing: -.16px;
  text-transform: uppercase;
  color: #111;
  background-color: #0000;
  border: 2px solid #111;
  border-radius: 40px;
  outline: #0000;
  align-items: center;
  padding: 16px 52px 16px 24px;
  font-family: DM Sans;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 112.5%;
}

.auth-reg-modal-field-input-icon {
  stroke: #111;
  fill: #0000;
  width: 18px;
  height: 18px;
  position: absolute;
  right: 24px;
}

.auth-reg-modal-sign {
  width: 100%;
  text-align: center;
  color: #f3f3f3;
  letter-spacing: -.16px;
  text-transform: uppercase;
  cursor: pointer;
  background: #111;
  border-radius: 40px;
  margin-bottom: 14px;
  padding: 16px 0;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 112.5%;
}

.auth-reg-modal-switcher {
  letter-spacing: -.14px;
  color: #11111180;
  text-transform: uppercase;
  cursor: pointer;
  background-color: #0000;
  border: none;
  justify-content: center;
  gap: 20px;
  font-size: 14px;
  font-style: normal;
  font-weight: 700;
  line-height: 128.571%;
  display: flex;
}

.auth-reg-modal-switcher-active {
  color: #4f2ee8;
  text-decoration-line: underline;
}

@media screen and (min-width: 768px) {
  .auth-reg-modal-callback-form {
    width: 579px;
    background: #f6f6f6;
    padding: 80px 40px 40px;
  }

  .auth-reg-modal-close {
    width: 28px;
    height: 28px;
    top: 24px;
    right: 24px;
  }

  .auth-reg-modal-input-box {
    gap: 28px;
    margin-bottom: 40px;
  }

  .auth-reg-modal-field-input-icon {
    stroke: #111;
    fill: #0000;
    position: absolute;
    right: 24px;
  }

  .auth-reg-modal-input {
    letter-spacing: -.18px;
    padding: 16px 62px 16px 24px;
    font-size: 18px;
    line-height: 133.333%;
  }

  .auth-reg-modal-field-input-icon {
    width: 28px;
    height: 28px;
    right: 24px;
  }

  .auth-reg-modal-sign {
    letter-spacing: -.18px;
    border-radius: 40px;
    padding: 20px 0;
    font-size: 18px;
    line-height: 133.333%;
  }

  .auth-reg-modal-switcher {
    letter-spacing: -.18px;
    justify-content: center;
    gap: 20px;
    font-size: 18px;
    line-height: 133.333%;
    display: flex;
  }
}

.main-categories-box {
  height: 228px;
  display: block;
  overflow-y: scroll;
}

.main-categories-box::-webkit-scrollbar {
  width: 12px;
}

.main-categories-box::-webkit-scrollbar-track {
  background-color: #0000;
}

.main-categories-box::-webkit-scrollbar-thumb {
  width: 8px;
  height: 114px;
  background: #e0e0e0;
  border-radius: 12px;
}

.main-categories-list {
  color: #1119;
  letter-spacing: -.32px;
  flex-direction: column;
  gap: 24px;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 112.5%;
  display: flex;
  overflow: hidden;
}

.main-categories-list-item {
  cursor: pointer;
}

.main-categories-list-item-active {
  color: #4f2ee8;
  text-transform: uppercase;
  font-weight: 700;
}

@media screen and (min-width: 768px) {
  .main-categories-box {
    height: 472px;
  }

  .main-categories-list {
    letter-spacing: -.36px;
    font-size: 18px;
    font-style: normal;
    line-height: 133.333%;
  }
}

@media screen and (min-width: 1440px) {
  .main-categories-list {
    gap: 32px;
  }
}

